.hero {
    background-color: $colorBackgroundDark;
    margin: 0 auto;
    max-width: 100%;
    padding-top: 181px;
    padding-bottom: 6px;
    @media screen and (min-width: 1024px) {
        padding-top: 188px;
    }
    @media screen and (min-width: 1280px) {
        max-width: 1280px;
        padding-top: 164px;        
    }
}

.hero--homepage {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image: $LinearGradientHeader, url('../images/hero_images/homepage-iphone@1x.jpg');
    @media screen and (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: $LinearGradientHeader, url('../images/hero_images/homepage-iphone@2x.jpg');
    }
    @media screen and (min-width: 1024px) {
        background-image: $LinearGradientHeader, url('../images/hero_images/homepage-ipad@1x.jpg');
    }
    @media screen and (min-width: 1024px) and (min-device-pixel-ratio: 2),
    (min-width: 1024px) and (min-resolution: 192dpi),
    (min-width: 1024px) and (min-resolution: 2dppx) {
        background-image: $LinearGradientHeader, url('../images/hero_images/homepage-ipad@2x.jpg');
    }
    @media screen and (min-width: 1280px) {
        background-image: $LinearGradientHeader, url('../images/hero_images/homepage@1x.jpg');
    }
    @media screen and (min-width: 1280px) and (min-device-pixel-ratio: 2),
    (min-width: 1280px) and (min-resolution: 192dpi),
    (min-width: 1280px) and (min-resolution: 2dppx) {
        background-image: $LinearGradientHeader, url('../images/hero_images/homepage@2x.jpg');
    }
}
