@font-face {
  font-family: 'Times New Roman Cyr';
  src: url('../fonts/TimesNRCyrMT.woff2') format('woff2'),
    url('../fonts/TimesNRCyrMT.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
